<template>
    <form @submit.prevent="ejecutarPeticion">
        <div class="form-group mb-3">
            <label for="exampleInputEmail1">Categora</label>
            <input type="text" 
                   class="form-control"
                   placeholder="Escribe la categora"
                   v-model="search">
        </div>
        <button type="submit" class="btn btn-primary" style="width: 100%">Search</button>
    </form>
</template>

<script>
export default {
    data: () => ({
        search: "",
    }),
    methods:{
        ejecutarPeticion(){
            this.$emit("accion", this.search);
        }
    }
}
</script>
